.app {
    background-image: url("background2.jpeg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
}

.name {
    padding: 50px 10px 2em;
    font-family: "Lucida Sans Unicode", "Lucida Grande", sans-serif;
    color: white;
    font-size: 50px;
    margin-top: -10px;
}

#content {
    position: relative;

}

#content img {
    position: absolute;
}

.ribbon {
    width: 125px;
    height: 125px;
    position: relative;
    margin-top: -125px;
    margin-left: -1050px;
}

.hr {
    width: auto;
    padding: 0px 10% 10px 20px;
    color: white;
    opacity: 0.5;
    padding-top: 2px;
    background-color: rgba(, 255, 255, 0);
    border: none;
    border-bottom: 3px solid black;
}

.showAboutMe {
    animation: 3s fadeIn;
    animation-fill-mode: forwards;

    visibility: hidden;
}


.showContactMeStart {
    animation: 4.5s fadeIn;
    display: inline-block;
    animation-fill-mode: forwards;

    visibility: hidden;
}

.showContactMe {
    animation: 6.5s fadeIn;
    animation-fill-mode: forwards;
    visibility: hidden;
}

@keyframes fadeIn {
    99% {
        visibility: hidden;
    }

    100% {
        visibility: visible;
    }
}

a {
    color: #2BC994;
}
