.terminal {
    background-color: #35383D;
    color: white;
    padding: 30px;
    border-radius: 5px;
    font-family: 'Ubuntu Mono', monospace;
    font-size: 20px;
    display: inline-block;
    margin-right: 50px;
    position: relative;
}

.outer {
  width: 90%;
  text-align: center;
}

.terminal-about {
    margin-top: -50px;
    min-height: 450px;
}

.terminal-contact {
    min-height: 240px;
}

.terminal-buttons {
    position: relative;
    display: -ms-inline-flexbox;
    display: inline-flex;
    float: right;
    margin-top: 5px;
    margin-right: 5px;
    margin-left: -60px;
}

.terminal-button {
    border-radius: 50%;
    width: 15px;
    height: 15px;
    display: inherit;
    margin-right: 5px;
}

.terminal-button-close {
    background-color: red;
}

.terminal-button-min {
    background-color: orange;
}

.terminal-button-max {
    background-color: green
}

.Typist p {
    display: inline-block;
}

.Typist {
    display: inline-block;
}

.Typist span {
    display: inline-block;
}

.terminal-header {
    width: calc(100% + 60px);
    margin: -30px -30px 13px -30px;
    text-align: center;
    border-radius: 4px 4px 0px 0px;
    background-color: white;
    color: #5f5f5f;
    position: relative;
}

/* lg+ screens */
@media (min-width: 992px) {
    .terminal {
        margin-left: 118px;
    }

    .terminal-contact {
        position: relative;
        left: 250px;
        top: -50px;
    }
}


@media (min-width: 768px) and (max-width: 991px) {
    .terminal-contact {
        position: relative;
        margin-left: 63%;
        width: 70%;
        margin-top: -13%;
    }

    .terminal {
      position: relative;
        max-width: 600px;
    }
}

.blinking-cursor {
  font-size: 20px;
  color: white;
  opacity: 1;
  animation-name: blink;
  animation-duration: 1000ms;
  animation-iteration-count: infinite;
}

@keyframes "blink" {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.cursor {
    position: relative;
}
.cursor i {
    position: absolute;
    width: 1px;
    height: 80%;
    background-color: gray;
    left: 5px;
    top: 10%;
    animation-name: blink;
    animation-duration: 800ms;
    animation-iteration-count: infinite;
    opacity: 1;
}

@keyframes blink {
    from { opacity: 1; }
    to { opacity: 0; }
}
